@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  background-color: #0e0f10;
  color: #fff;
  font-family: 'Lexend Deca', sans-serif;
  font-style: normal;
}

.home-section {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.heading {
  font-size: 4rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

.sub-heading {
  font-size: 1.5rem;
}

.svg{
  position: absolute;
  bottom: 100;
  right: 0;
  padding-left: 700px;
  padding-top: 0px;
}

/* Add more specific styles for other sections as needed */

/* Footer styling */
.footer {
  background-color: #333;
  color: #fff;
  padding: 2rem;
  text-align: center;
}


